.sendForm {
    margin-top: 73px;
    display: flex;
    justify-content: center;
    gap: 40px;
}

.textarea {

    width: 976px;
    height: 40px;
    flex-shrink: 0;
    border-radius: 20px;
    background: #F5F7FB;
    box-shadow: 0px 5px 20px 0px rgba(29, 33, 38, 0.03), 0px 1px 2px 0px rgba(29, 33, 38, 0.10);
    padding: 12px  ;
    scrollbar-gutter: unset;
    overflow: hidden;
    max-height: 100px;

}

.button {
    width: 120px;
    height: 40px;
    flex-shrink: 0;
    border-radius: 20px;
    background: #06C;
    color: #FFF;
    font-family: Montserrat, sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

